@import url("https://fonts.googleapis.com/css2?family=Caveat:wght@400;600&display=swap");

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Caveat", cursive;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
  color: #fff;
  background-color: midnightblue;
  min-height: 100vh;
  display: flex;
  justify-content: center;
}

.container {
    width: min(80%,600px);
    border: 1px solid #efefef;
    border-radius: 5px;
    margin: 4rem auto;
    padding: 2rem 4rem;
    text-align: center;
}

#root {width: 100%;}


/* Search Field */
.search-field {
    width: 100%;
    margin-bottom: 3rem;
}

.search-field input {
    width: 80%;
    background-color: #efefef;
    border: none;
    outline: none;
    border-radius: 7px;
    padding: 10px 1rem;
    color: #333;
    font-family: inherit;
    font-size: 20px;
}


/* Results */
.results {
    width: 100%;
}

.results__list {
    list-style: none;
}

.results__item {
    font-size: 1.2rem;
    margin-bottom: 10px;
}
